/**
 * Visual Viewport ÂêåÊ≠•Â∑•ÂÖ∑
 * Â∞ÜiOSÁöÑVisual ViewportÈ´òÂ∫¶ÂêåÊ≠•Âà∞window.innerHeight
 */

class ViewportSync {
  constructor() {
    this.originalInnerHeight = window.innerHeight;
    this.isPatched = false;
    
    if (this.isIOSDevice() && window.visualViewport) {
      this.patchInnerHeight();
    }
  }

  isIOSDevice() {
    return /iPad|iPhone|iPod/.test(navigator.userAgent) || 
           (navigator.userAgent.includes('Mac') && 'ontouchend' in document);
  }

  patchInnerHeight() {
    if (this.isPatched) return;
    
    // ‰øùÂ≠òÂéüÂßãÂÄº
    this.originalBodyOffsetHeight = document.body ? document.body.offsetHeight : 0;
    this.originalDocumentHeight = document.documentElement ? document.documentElement.scrollHeight : 0;
    
    // ‰øùÂ≠òÂéüÂßãÊñπÊ≥ï
    if (document.documentElement) {
      this.originalGetBoundingClientRect = document.documentElement.getBoundingClientRect;
    }
    
    // ÈáçÊñ∞ÂÆö‰πâ window.innerHeight
    Object.defineProperty(window, 'innerHeight', {
      get: () => {
        return window.visualViewport ? window.visualViewport.height : this.originalInnerHeight;
      },
      configurable: true,
      enumerable: true
    });

    // ÈáçÊñ∞ÂÆö‰πâ body.offsetHeight
    if (document.body) {
      Object.defineProperty(document.body, 'offsetHeight', {
        get: () => {
          return window.visualViewport ? window.visualViewport.height : this.originalBodyOffsetHeight;
        },
        configurable: true
      });
    }

    // ÈáçÊñ∞ÂÆö‰πâÂ§ö‰∏™documentÈ´òÂ∫¶Â±ûÊÄß
    if (document.documentElement) {
      const element = document.documentElement;
      
      // Â∞ùËØïÈáçÂÜôscrollHeight
      try {
        Object.defineProperty(element, 'scrollHeight', {
          get: () => {
            return window.visualViewport ? window.visualViewport.height : this.originalDocumentHeight;
          },
          configurable: true
        });
      } catch (e) {
        console.warn('Êó†Ê≥ïÈáçÂÜôscrollHeight:', e);
      }
      
      // ÈáçÂÜôclientHeight
      try {
        Object.defineProperty(element, 'clientHeight', {
          get: () => {
            return window.visualViewport ? window.visualViewport.height : this.originalDocumentHeight;
          },
          configurable: true
        });
      } catch (e) {
        console.warn('Êó†Ê≥ïÈáçÂÜôclientHeight:', e);
      }
      
      // ÈáçÂÜôoffsetHeight
      try {
        Object.defineProperty(element, 'offsetHeight', {
          get: () => {
            return window.visualViewport ? window.visualViewport.height : this.originalDocumentHeight;
          },
          configurable: true
        });
      } catch (e) {
        console.warn('Êó†Ê≥ïÈáçÂÜôoffsetHeight:', e);
      }
      
      // ÈáçÂÜôgetBoundingClientRectÊñπÊ≥ï
      const originalGetBoundingClientRect = element.getBoundingClientRect;
      element.getBoundingClientRect = function() {
        const rect = originalGetBoundingClientRect.call(this);
        if (window.visualViewport) {
          return {
            ...rect,
            height: window.visualViewport.height,
            bottom: rect.top + window.visualViewport.height
          };
        }
        return rect;
      };
    }

    // ÁõëÂê¨Visual ViewportÂèòÂåñÂπ∂ÂêåÊ≠•ÊâÄÊúâÈ´òÂ∫¶
    const handleViewportChange = () => {
      const visualHeight = window.visualViewport.height;
      
      // ÂêåÊ≠•bodyÂíåhtmlÈ´òÂ∫¶
      this.syncBodyHeight(visualHeight);
      
      // Ëß¶Âèëresize‰∫ã‰ª∂ÔºåËÆ©‰æùËµñinnerHeightÁöÑ‰ª£Á†ÅÈáçÊñ∞ËÆ°ÁÆó
      const resizeEvent = new Event('resize');
      window.dispatchEvent(resizeEvent);
      
      console.log(`üì± ÂÖ®ÈÉ®È´òÂ∫¶Â∑≤ÂêåÊ≠•: ${visualHeight}px`);
    };

    window.visualViewport.addEventListener('resize', handleViewportChange);
    window.visualViewport.addEventListener('scroll', handleViewportChange);
    
    // ÂàùÂßãÂåñÊó∂ÂêåÊ≠•‰∏ÄÊ¨°
    this.syncBodyHeight(window.visualViewport.height);
    
    this.isPatched = true;
    console.log('‚úÖ Visual ViewportÂ∑≤ÂêåÊ≠•Âà∞window.innerHeightÂíåbodyÈ´òÂ∫¶');
  }

  // ÊÅ¢Â§çÂéüÂßãinnerHeight (Â¶ÇÊûúÈúÄË¶Å)
  restore() {
    if (!this.isPatched) return;
    
    // ÊÅ¢Â§çwindow.innerHeight
    Object.defineProperty(window, 'innerHeight', {
      value: this.originalInnerHeight,
      writable: false,
      configurable: true,
      enumerable: true
    });
    
    // ÊÅ¢Â§çbody.offsetHeight
    if (document.body) {
      Object.defineProperty(document.body, 'offsetHeight', {
        value: this.originalBodyOffsetHeight,
        writable: false,
        configurable: true
      });
    }
    
    // ÊÅ¢Â§çdocumentElementÁöÑÊâÄÊúâÈ´òÂ∫¶Â±ûÊÄß
    if (document.documentElement) {
      const element = document.documentElement;
      
      // ÊÅ¢Â§çscrollHeight
      try {
        Object.defineProperty(element, 'scrollHeight', {
          value: this.originalDocumentHeight,
          writable: false,
          configurable: true
        });
      } catch (e) {
        console.warn('Êó†Ê≥ïÊÅ¢Â§çscrollHeight:', e);
      }
      
      // ÊÅ¢Â§çclientHeight
      try {
        delete element.clientHeight;
      } catch (e) {
        console.warn('Êó†Ê≥ïÊÅ¢Â§çclientHeight:', e);
      }
      
      // ÊÅ¢Â§çoffsetHeight
      try {
        delete element.offsetHeight;
      } catch (e) {
        console.warn('Êó†Ê≥ïÊÅ¢Â§çoffsetHeight:', e);
      }
      
      // ÊÅ¢Â§çgetBoundingClientRect
      if (this.originalGetBoundingClientRect) {
        element.getBoundingClientRect = this.originalGetBoundingClientRect;
      }
    }
    
    // ÊÅ¢Â§çÊ†∑Âºè
    this.restoreBodyHeight();
    
    this.isPatched = false;
    console.log('üîÑ Â∑≤ÊÅ¢Â§çÊâÄÊúâÂéüÂßãÈ´òÂ∫¶Â±ûÊÄß');
  }

  // Ëé∑ÂèñÁúüÂÆûÁöÑÂéüÂßãinnerHeight
  getOriginalInnerHeight() {
    return this.originalInnerHeight;
  }

  // Ëé∑ÂèñÂΩìÂâçVisual ViewportÈ´òÂ∫¶
  getVisualViewportHeight() {
    return window.visualViewport ? window.visualViewport.height : this.originalInnerHeight;
  }

  // Ëé∑ÂèñÈîÆÁõòÈ´òÂ∫¶
  getKeyboardHeight() {
    return this.originalInnerHeight - this.getVisualViewportHeight();
  }

  // ÂêåÊ≠•bodyÂíåhtmlÈ´òÂ∫¶
  syncBodyHeight(height) {
    // ËÆæÁΩÆhtmlÈ´òÂ∫¶
    if (document.documentElement) {
      document.documentElement.style.height = `${height}px`;
      document.documentElement.style.maxHeight = `${height}px`;
    }
    
    // ËÆæÁΩÆbodyÈ´òÂ∫¶
    if (document.body) {
      document.body.style.height = `${height}px`;
      document.body.style.maxHeight = `${height}px`;
      document.body.style.overflow = 'hidden'; // Èò≤Ê≠¢ÊªöÂä®
    }
    
    // ËÆæÁΩÆ#appÈ´òÂ∫¶ (VueÂ∫îÁî®Ê†πÂÖÉÁ¥†)
    const app = document.getElementById('app');
    if (app) {
      app.style.height = `${height}px`;
      app.style.maxHeight = `${height}px`;
    }
    
    // ËÆæÁΩÆCSSÂèòÈáè
    document.documentElement.style.setProperty('--viewport-height', `${height}px`);
    document.documentElement.style.setProperty('--keyboard-height', `${this.originalInnerHeight - height}px`);
  }

  // ÊÅ¢Â§çÂéüÂßãÈ´òÂ∫¶
  restoreBodyHeight() {
    if (document.documentElement) {
      document.documentElement.style.height = '';
      document.documentElement.style.maxHeight = '';
    }
    
    if (document.body) {
      document.body.style.height = '';
      document.body.style.maxHeight = '';
      document.body.style.overflow = '';
    }
    
    const app = document.getElementById('app');
    if (app) {
      app.style.height = '';
      app.style.maxHeight = '';
    }
  }
}

// iOSËæìÂÖ•Ê≥ïÂ∑•ÂÖ∑Ê†èÈöêËóèÂ∑•ÂÖ∑
class IOSInputToolbarHider {
  constructor() {
    if (this.isIOSDevice()) {
      this.init();
    }
  }

  isIOSDevice() {
    return /iPad|iPhone|iPod/.test(navigator.userAgent) || 
           (navigator.userAgent.includes('Mac') && 'ontouchend' in document);
  }

  init() {
    // Á´ãÂç≥Â§ÑÁêÜÁé∞ÊúâËæìÂÖ•Ê°Ü
    this.processExistingInputs();
    
    // ÁõëÂê¨Êñ∞Ê∑ªÂä†ÁöÑËæìÂÖ•Ê°Ü
    this.observeNewInputs();
    
    // ÁõëÂê¨ÁÑ¶ÁÇπ‰∫ã‰ª∂
    this.bindFocusEvents();
    
    console.log('üçé iOSËæìÂÖ•Ê≥ïÂ∑•ÂÖ∑Ê†èÈöêËóèÂô®Â∑≤ÂêØÂä®');
  }

  processExistingInputs() {
    const inputs = document.querySelectorAll('input, textarea, select, [contenteditable="true"]');
    inputs.forEach(input => this.applyIOSFix(input));
  }

  applyIOSFix(element) {
    if (!element) return;
    
    // ËÆæÁΩÆÂÖ≥ÈîÆÂ±ûÊÄß
    const attributes = {
      'autocomplete': 'off',
      'autocorrect': 'off', 
      'autocapitalize': 'off',
      'spellcheck': 'false'
    };
    
    Object.entries(attributes).forEach(([key, value]) => {
      element.setAttribute(key, value);
    });
    
    // ËÆæÁΩÆÊ†∑Âºè
    const styles = {
      '-webkit-appearance': 'none',
      'appearance': 'none',
      'fontSize': '16px',
      '-webkit-text-size-adjust': '100%',
      '-webkit-touch-callout': 'none',
      '-webkit-user-select': 'text',
      '-webkit-user-modify': 'read-write-plaintext-only'
    };
    
    Object.entries(styles).forEach(([key, value]) => {
      element.style.setProperty(key, value, 'important');
    });
  }

  observeNewInputs() {
    // ‰ΩøÁî®MutationObserverÁõëÂê¨DOMÂèòÂåñ
    const observer = new MutationObserver((mutations) => {
      mutations.forEach((mutation) => {
        mutation.addedNodes.forEach((node) => {
          if (node.nodeType === Node.ELEMENT_NODE) {
            // Ê£ÄÊü•Êñ∞Ê∑ªÂä†ÁöÑËäÇÁÇπÊòØÂê¶ÊòØËæìÂÖ•Ê°Ü
            if (this.isInputElement(node)) {
              this.applyIOSFix(node);
            }
            
            // Ê£ÄÊü•Êñ∞Ê∑ªÂä†ËäÇÁÇπÁöÑÂ≠êÂÖÉÁ¥†
            const inputs = node.querySelectorAll ? node.querySelectorAll('input, textarea, select, [contenteditable="true"]') : [];
            inputs.forEach(input => this.applyIOSFix(input));
          }
        });
      });
    });

    observer.observe(document.body, {
      childList: true,
      subtree: true
    });
  }

  bindFocusEvents() {
    // Âú®ËæìÂÖ•Ê°ÜËÅöÁÑ¶Êó∂ÂÜçÊ¨°Â∫îÁî®‰øÆÂ§ç
    document.addEventListener('focusin', (e) => {
      if (this.isInputElement(e.target)) {
        // Âª∂ËøüÂ∫îÁî®ÔºåÁ°Æ‰øùÂÖÉÁ¥†ÂÆåÂÖ®Ê∏≤Êüì
        setTimeout(() => {
          this.applyIOSFix(e.target);
        }, 0);
      }
    });
  }

  isInputElement(element) {
    if (!element || !element.tagName) return false;
    
    const inputTags = ['INPUT', 'TEXTAREA', 'SELECT'];
    return inputTags.includes(element.tagName.toUpperCase()) || 
           element.contentEditable === 'true';
  }
}

// Ëá™Âä®ÂàùÂßãÂåñ
const viewportSync = new ViewportSync();
const iosInputFixer = new IOSInputToolbarHider();

// Êö¥Èú≤Âà∞ÂÖ®Â±Ä
window.viewportSync = viewportSync;
window.iosInputFixer = iosInputFixer;

export default ViewportSync;